/*Tree list*/

var React = require('react');
var PureRenderMixin = require('react/addons').addons.PureRenderMixin;
var Item=require('./treeListItem.jsx');
var _=require('lodash');
var If=require('../commons/if.jsx');
var CommonsMixins=require('./commonsMixins.jsx');
var KeywordSearch=require('./keywordSearch.jsx');
var SelectAllNone=require('./allNoneSelector.jsx');
var SelectionCounter = require('./selectionCounter.jsx');

var TreeView =  React.createClass({displayName: "TreeView",

  _onItemChange: function(item, selected) {
    this.props.onItemChange(item, selected);
  },

  _getNestedSelected: function(){
    return _.filter(this.props.nested, function(item){
        return item.selected? true : false;
      });
  },

  _onToggle:function(){
    this.setState(_.assign(this.state,{expanded:!this.state.expanded}));
    this.forceUpdate();
  },

  getInitialState: function() {
    return {
      expanded: this.props.expanded,
      items: [],
      selected: [],
      filter: '',
    };
  },
  
  render: function() {
    //console.log("render location -> "+this.props.name);
    return ( 
      React.createElement("div", null, 
        React.createElement(Item, React.__spread({},  this.props, 
          {showOnlySelected: this.props.showOnlySelected, 
          onItemChange: this._onItemChange, 
          expanded: this.state.expanded, 
          onToggle: this._onToggle, 
          childrenTotal: this.props.nested? this.props.nested.length : 0, 
          childrenSelected: this.props.nested? this._getNestedSelected().length : 0})), 
          (this.props.nested && this.state.expanded)? 
            React.createElement("ul", {className: "filter-list tabbed"}, 
            
              this.props.nested.map(function(item) {   
                  return (React.createElement("li", null, React.createElement(TreeView, React.__spread({},  item, {showOnlySelected: this.props.showOnlySelected, onItemChange: this.props.onItemChange, expanded: false}))))            
              }.bind(this))
            
            )
          :null
      )
    );    
  }
});

/*end tree view*/

module.exports = React.createClass({displayName: "exports",

  mixins: [CommonsMixins, PureRenderMixin],
  
  componentDidUpdate: function() {
    $(this.getDOMNode()).find('.filter-list-container').mCustomScrollbar({theme:"inset-dark"});
  },

  getInitialState: function() {
    return {
      itemsTree: []
    };
  },

  render: function() {
    console.log("render -> treeList")
    var items = this.state.itemsTree || [];
    var lowestLevel = this.state[this.state.lowestLevel];
    var lowerLevelCounterSelected =  0;
    var lowerLevelCounterTotal = 0;
    if (lowestLevel){
      lowerLevelCounterTotal = lowestLevel.length;
      lowerLevelCounterSelected =  _.filter(lowestLevel, function(it){return it.selected}).length;  
    }
    if (this.props.active){
      return ( 
        React.createElement("div", {className: "tab-content"}, 
          React.createElement("div", {className: "tab-pane fade active in"}, 
            React.createElement("div", {className: "filter-group-panel selected"}, 
              
              React.createElement("div", {className: "filter-group-panel-header"}, 
                React.createElement("span", {className: "filter-label", role: "label"}, React.createElement(Message, {message: this.props.label})), 
                React.createElement(SelectionCounter, {selected: lowerLevelCounterSelected, total: lowerLevelCounterTotal, onCounterClicked: this._onCounterClicked}), 
                React.createElement(SelectAllNone, {onSelectAll: this._onSelectAll, onSelectNone: this._onSelectNone})
              ), 
              React.createElement(KeywordSearch, {onSearch: this._onSearch, onSearchEnterKey: this._onSearchEnterKey}), 
              React.createElement("div", {className: "filter-list-container"}, 
              
                items.map(function(item) {   
                  return (
                    React.createElement("div", {className: ""}, 
                      React.createElement(TreeView, React.__spread({},  item, {showOnlySelected: this.state.showOnlySelected, onItemChange: this._onItemChange, expanded: false}))
                    ))            
                }.bind(this))
              
              )
            )
          )
        )
      );    
    } else {
      return null;
    }
  }
});